*{
    margin: 0;
    padding: 0;
}
.Maincontent{
    display: flex;
    justify-content: space-evenly;
    margin-top: 20px;
}
li{
    text-decoration: none;
    list-style: none;
    margin-left: 10px;
}
.header-right ul{
    display: flex;
    margin-left: 20px;
}
li a{
    text-decoration: none;
    color: black;
}
li a:hover{
    font-size: large;
    color: chocolate;
}

/* footer */
/* Footer styling */
.footer {
    width: 100%;
    background-color: #333333;
    color: white;
    padding: 20px 0;
    /* position: fixed; */
    bottom: 0;
    left: 0;
    text-align: center;
}

.footer-content {
    display: flex;
    justify-content: space-around;
    margin-bottom: 20px;
}

.footer-section {
    width: 30%;
}

.footer-section h3 {
    margin-bottom: 15px;
    font-size: 18px;
    color: #ffffff;
}

.footer-section p {
    font-size: 14px;
    color: #cccccc;
    margin-bottom: 10px;
}

.footer-section a {
    display: block;
    color: #ffffff;
    text-decoration: none;
    margin-bottom: 5px;
}

.footer-section a:hover {
    color: #ffdd57;
}

.footer-bottom {
    border-top: 1px solid #444444;
    padding-top: 10px;
    font-size: 14px;
    color: #cccccc;
}
